// ======================================

// ======================================

import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { LoginComponent } from "./components/login/login.component";
import { HomeComponent } from "./components/home/home.component";
import { CustomersComponent } from "./components/customers/customers.component";
import { ProductsComponent } from "./components/products/products.component";
import { OrdersComponent } from "./components/orders/orders.component";
import { SettingsComponent } from "./components/settings/settings.component";
import { AboutComponent } from "./components/about/about.component";
import { Thankyou } from "./components/thankyou/thankyou.component";

import { NotFoundComponent } from "./components/not-found/not-found.component";
import { ParticipantComponent } from "./components/participant/participant.component";
import { ScoreComponent } from "./components/score/score.component";
import { ParticipantsListComponent } from "./components/participantslist/participantslist.component";
import { DashboardComponent } from "./components/dashboard/dashboard.component";
import { AuthService } from './services/auth.service';
import { AuthGuard } from './services/auth-guard.service';
import { SurveyBoardComponent } from "./components/DashboardControls/surveyboard.component";
import { SurveyBoardMaintain } from "./components/DashboardControls/surveyboardMaintain.component";
import { MemberManagement } from "./components/DashboardControls/memberManagement.component";
import { MemberEditComponent } from "./components/DashboardControls/memberEdit.component";





@NgModule({
    imports: [
        RouterModule.forRoot([
            { path: "", component: HomeComponent, canActivate: [AuthGuard], data: { title: "Home" } },
            { path: "login", component: LoginComponent, data: { title: "Login" } },
            { path: "customers", component: CustomersComponent, canActivate: [AuthGuard], data: { title: "Customers" } },
            { path: "products", component: ProductsComponent, canActivate: [AuthGuard], data: { title: "Products" } },
            { path: "orders", component: OrdersComponent, canActivate: [AuthGuard], data: { title: "Orders" } },
            { path: "settings", component: SettingsComponent, canActivate: [AuthGuard], data: { title: "Settings" } },
            { path: "about", component: AboutComponent, data: { title: "About Us" } },
            { path: "thanks", component: Thankyou, data: { title: "Thank You" } },
            { path: "home", redirectTo: "/", pathMatch: "full" },
            { path: "participant", component: ParticipantComponent, data: { title: "Register Participant" } },
            { path: "score", component: ScoreComponent, data: { title: "Score of the Participant" } },
            { path: "participantslist", component: ParticipantsListComponent, data: { title: "List of Participants" } },
            { path: "dashboard/:id", component: SurveyBoardComponent, data: { title: "Survey dashboard" } },
            { path: "surveymaintain", component: SurveyBoardMaintain, data: { title: "Survey Maintainance" } },
            { path: "participants", component: MemberManagement, data: { title: "Participants" } },
            { path: "participants", component: MemberManagement, data: { title: "Participants" } },
            { path: "memberedit/:id", component: MemberEditComponent, data: { title: "memberEdit" } },

            { path: "**", component: NotFoundComponent, data: { title: "Page Not Found" } },
        ])
    ],
    exports: [
        RouterModule
    ],
    providers: [
        AuthService, AuthGuard
    ]
})
export class AppRoutingModule { }


